@mixin button-reset {
    border: 0 none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: transparent;
}
@mixin box-sizing-border {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
@mixin ellipsis-multi($line) {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
    overflow: hidden;
    text-overflow: ellipsis;
}
@mixin ellipsis-single {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
@mixin blind {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
}
@mixin flex-container {
    display: flex;
    display: -ms-flexbox;
}
@mixin flex-direction($value: row) {
    flex-direction: $value;
    -ms-flex-direction: $value;
}
@mixin flex-wrap($value: nowrap) {
    flex-wrap: $value;
    -webkit-flex-wrap: $value;
    @if $value == nowrap {
        -ms-flex-wrap: nowrap;
    } @else {
        -ms-flex-wrap: $value;
    }
}
@mixin flex($args: ()) {
    $flexitems: $args;
    @if $args == initial {
        $flexitems: 0 1 auto;
    } @else if $args == auto {
        $flexitems: 1 1 auto;
    } @else if $args == none {
        $flexitems: 0 0 auto;
    } @else if length($args) == 1 {
        $flexitems: $args 1 0%;
    }
    flex: $flexitems;
    -ms-flex: $flexitems;
}
@mixin flex-wrap($value: nowrap) {
    flex-wrap: $value;
    @if $value == nowrap {
        -ms-flex-wrap: nowrap;
    } @else {
        -ms-flex-wrap: $value;
    }
}
@mixin justify-content($value: flex-start) {
    justify-content: $value;
    @if $value == flex-start {
        -ms-flex-pack: start;
    } @else if $value == flex-end {
        -ms-flex-pack: end;
    } @else if $value == space-between {
        -ms-flex-pack: justify;
    } @else if $value == space-around {
        -ms-flex-pack: distribute;
    } @else {
        -ms-flex-pack: $value;
    }
}
@mixin align-items($value: stretch) {
    align-items: $value;
    @if $value == flex-start {
        -ms-flex-align: start;
    } @else if $value == flex-end {
        -ms-flex-align: end;
    } @else {
        -ms-flex-align: $value;
    }
}
@mixin order($value) {
    order: $value;
    -ms-flex-order: $value;
    -webkit-order: $value;
}

@mixin blind {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
}
